@use '../util' as *;


.header{
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(36);


    @include breakpoint(medium) {
        display: flex;
        justify-content: space-between;
    }

    &__title{
        width: 100%;

        @include breakpoint(medium){
            width: auto;
        }
    }

    &__subtitle{
        font-size: rem(14);
        font-weight: bold;
        color: var(--text-color2);
    }
}