@use '../util' as *;

.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {

        grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoint(large) {

        grid-template-columns: repeat(4, 1fr);
    }
}

.card {
    position: relative;
    overflow: hidden;
    border-radius: rem(5);
    background: var(--card-bg);
    color: var(--dark-text1);
    padding: rem(25);
    text-align: center;
    transition: background 150ms ease-in-out ;
    flex: 1;
    cursor: pointer;


    &:hover {
        background: var(--card-hover);
    }

    &--facebook {
        border-top: rem(5) solid var(--facebook);
    }
    &--twitter {
        border-top: rem(5) solid var(--twitter);
    }
    &--instagram {
        padding-top: rem(30);
        &::before {
            content: '';
            height: rem(5);
            width: 100%;
            position: absolute;
            display: block;
            left: 0;
            top: 0;
            background: linear-gradient(
                225deg,
                var(--instagram-end),
                var(--instagram-middle),
                var(--instagram-start) 
            );

        }
        //border-top: rem(5) solid var(--instagram);
    }
    &--youtube {
        border-top: rem(5) solid var(--youtube);
    }

    &__platform {
        display: flex;
        justify-content: center;
        align-items: center;
        height: rem(20);
        margin-bottom: rem(28);
        margin-top: rem(5);
    }

    &__subtitle {
        font-size: rem(14);
        font-weight: 700;
    }

    &__icon {
        margin: rem(8);
        &--facebook {

        }

        &--twitter {
            
        }

        &--instagram {
            
        }

        &--youtube {
            
        }


    }

    &__username {
        font-size: rem(12);
        font-weight: 700;
    }

    &__follower {
        margin-bottom: rem(25);
    }

    &__count {
        color: var(--text-color);
        font-weight: 700;
        letter-spacing: rem(-2);
        line-height: 1;
        margin-bottom: rem(4);

        &--big {
            font-size: rem(56);
        }

        &--small {
            font-size: rem(32);
        }
    }

    &__label {
        color: var(--dark-text1);
        font-size: rem(12);
        letter-spacing: rem(5);
        font-weight: 400;
        text-transform: uppercase;
    }

    &__change {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(12);
        font-weight: 700;

        &--up {
            color: var(--limegreen);
        }

        &--down {
            color: var(--brighred);
        }

        img {
            margin-right: rem(4);
        }
    }
}